import React, { useEffect } from "react";
import errorStyle from "./Error.module.css";
import errorSvg from "../../Assets/images/404.svg";
import { Link } from "react-router-dom";

const {} = errorStyle;

export default function Error() {
  useEffect(() => {
    document.title = "404 Not Found";
  }, []);

  return (
    <>
      <section className="error min-vh-100 d-flex justify-content-center align-items-center">
        <div className="container py-5 h-100 d-flex gap-3 flex-column justify-content-center align-items-center">
          <img src={errorSvg} className="w-25" alt="Page Not Found" />
          <h2 className="text-uppercase">Page Not Found</h2>
          <div className="error-message">
            <p className="mb-1">We looked everywhere for this page.</p>
            <p className="mb-1">Are you sure the website URL is correct?</p>
            <p className="mb-1">Get in touch with the site owner</p>
          </div>
          <button className="btn btn-submit text-capitalize">
            <Link to="" className="text-white">
              Go back Home
            </Link>
          </button>
        </div>
      </section>
    </>
  );
}
