import React, { useEffect, useState } from "react";
import Box from "../Box/Box";
import Divider from "../Divider/Divider";
import portfolioStyle from "./Portfolio.module.css";

const { portfolio, container, image } = portfolioStyle;

export default function Portfolio() {
  function showBox(e) {
    const box = document.querySelector(".boxContainer");
    const boxImage = document.querySelector(".box img");
    const boxTitle = document.querySelector(".box h2");
    const imageSrc = e.target.children[0].getAttribute("src");
    const imagealt = e.target.children[0].getAttribute("alt");
    boxTitle.textContent = imagealt;
    boxImage.setAttribute("src", imageSrc);
    document.body.style.overflow = "hidden";
    box.style.overflowY = "scroll";
    box.classList.remove("d-none");
  }

  function closeBox() {
    const box = document.querySelector(".boxContainer");
    document.body.style.overflow = "auto";
    box.classList.add("d-none");
  }

  useEffect(() => {
    const allImages = document.querySelectorAll(".image-box");
    for (let image of allImages) {
      image.addEventListener("click", (e) => {
        showBox(e);
      });
    }

    document.body.addEventListener("keydown", (e) => {
      if (e.key === "Escape") closeBox();
    });

    const box = document.querySelector(".boxContainer");
    box.addEventListener("click", function (e) {
      if (e.target.classList.contains("boxContainer")) {
        closeBox();
      }
    });
  }, []);

  return (
    <>
      <section className={`${portfolio} py-5 position-relative`} id="portfolio">
        <div className={`container w-75 ${container} py-4`}>
          <h2 className="main-title">Portfolio</h2>
          <Divider />
          <div className="row g-5 pt-5">
            <div className="col-lg-4 col-md-6 image-box">
              <div className={`${image} rounded-2 overflow-hidden`}>
                <img
                  src={require("../../Assets/images/cabin.png")}
                  className={`w-100`}
                  alt="LOG CABIN"
                />
              </div>
            </div>
            <div className="col-lg-4 col-md-6 image-box">
              <div className={`${image} rounded-2 overflow-hidden`}>
                <img
                  src={require("../../Assets/images/cake.png")}
                  className={`w-100`}
                  alt="TASTY CAKE"
                />
              </div>
            </div>
            <div className="col-lg-4 col-md-6 image-box">
              <div className={`${image} rounded-2 overflow-hidden`}>
                <img
                  src={require("../../Assets/images/circus.png")}
                  className={`w-100`}
                  alt="CIRCUS TENT"
                />
              </div>
            </div>
            <div className="col-lg-4 col-md-6 image-box">
              <div className={`${image} rounded-2 overflow-hidden`}>
                <img
                  src={require("../../Assets/images/game.png")}
                  className={`w-100`}
                  alt="CONTROLLER"
                />
              </div>
            </div>
            <div className="col-lg-4 col-md-6 image-box">
              <div className={`${image} rounded-2 overflow-hidden`}>
                <img
                  src={require("../../Assets/images/safe.png")}
                  className={`w-100`}
                  alt="LOCKED SAFE"
                />
              </div>
            </div>
            <div className="col-lg-4 col-md-6 image-box">
              <div className={`${image} rounded-2 overflow-hidden`}>
                <img
                  src={require("../../Assets/images/submarine.png")}
                  className={`w-100`}
                  alt="SUBMARINE"
                />
              </div>
            </div>
          </div>
        </div>
        <Box close={closeBox} />
      </section>
    </>
  );
}
