import React, { useEffect } from "react";
import { Link } from "react-scroll";
import NavbarStyle from "./Navbar.module.css";

export default function Navbar() {
  function shrinkNav() {
    const navbar = document.querySelector(".navbar");
    const logo = document.querySelector(".logo");
    window.addEventListener("scroll", function () {
      const scrollValue = window.scrollY;
      if (scrollValue > 50) {
        navbar.style.paddingBlock = "10px";
        logo.style.fontSize = "25px";
      } else {
        navbar.style.paddingBlock = "25px";
        logo.style.fontSize = "32px";
      }
    });
  }

  useEffect(() => {
    shrinkNav();
  }, []);

  return (
    <>
      <nav
        className={`navbar ${NavbarStyle.navbar} navbar-expand-lg fixed-top`}
      >
        <div className="container w-75">
          <Link
            className={`${NavbarStyle.logo} ${NavbarStyle["nav-link"]} logo`}
            to="/"
            spy={true}
            smooth={true}
            duration={500}
            offset={100}
          >
            Start React
          </Link>
          <button
            className={`navbar-toggler ${NavbarStyle["navbar-toggler"]}`}
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            Menu <i className="fa-solid fa-bars"></i>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav ms-auto mb-2 mb-lg-0 mt-lg-0 mt-2">
              <li className="nav-item mx-2">
                <Link
                  className={`nav-link p-3 ${NavbarStyle["nav-link"]}`}
                  to="portfolio"
                  spy={true}
                  smooth={true}
                  duration={500}
                  offset={-80}
                >
                  Portfolio
                </Link>
              </li>
              <li className="nav-item mx-2">
                <Link
                  className={`nav-link p-3 ${NavbarStyle["nav-link"]}`}
                  to="about"
                  spy={true}
                  smooth={true}
                  duration={500}
                  offset={-80}
                >
                  About
                </Link>
              </li>
              <li className="nav-item mx-2">
                <Link
                  className={`nav-link p-3 ${NavbarStyle["nav-link"]}`}
                  to="contact"
                  spy={true}
                  smooth={true}
                  duration={500}
                  offset={-80}
                >
                  Contact
                </Link>
              </li>
            </ul>
          </div>
        </div>
      </nav>
    </>
  );
}
