import React, { useEffect } from "react";
import Divider from "../Divider/Divider";
import contactStyle from "./Contact.module.css";

const { contact, container, form, name, email, phone, message } = contactStyle;

export default function Contact() {
  const nameRegex = /^[a-zA-Z]{3,}(\s[a-zA-Z]{3,})*$/;
  const emailRegex = /^\w{3,}@[a-zA-Z]{3,}\.[a-zA-Z]{2,}$/;
  const phoneRegex = /^(\+2)?01[0125][0-9]{8}$/;

  function animateLabel() {
    const inputs = document.querySelectorAll("input , textarea");
    for (let input of inputs) {
      input.addEventListener("input", function () {
        if (input.value.length > 0) {
          input.previousElementSibling.style.opacity = 1;
          input.previousElementSibling.style.top = "-15px";
        } else {
          input.previousElementSibling.style.opacity = 0;
          input.previousElementSibling.style.top = "50%";
        }
      });

      input.addEventListener("blur", function () {
        if (input.value.length > 0) {
          input.previousElementSibling.style.opacity = 1;
          input.previousElementSibling.style.top = "-15px";
          input.previousElementSibling.style.color = "#2c3e50";
        }
      });
    }
  }

  function validate(element, regex) {
    if (element.value.length > 0) {
      if (regex.test(element.value)) {
        element.classList.remove("is-invalid");
        element.classList.add("is-valid");
      } else {
        element.classList.remove("is-valid");
        element.classList.add("is-invalid");
      }
    } else {
      element.classList.remove("is-valid");
      element.classList.remove("is-invalid");
    }
  }

  useEffect(() => {
    animateLabel();
    const nameInput = document.querySelector("input[type='text']");
    nameInput.addEventListener("input", () => {
      validate(nameInput, nameRegex);
    });
    const emailInput = document.querySelector("input[type='email']");
    emailInput.addEventListener("input", () => {
      validate(emailInput, emailRegex);
    });
    const phoneInput = document.querySelector("input[type='tel']");
    phoneInput.addEventListener("input", () => {
      validate(phoneInput, phoneRegex);
    });
  }, []);

  return (
    <>
      <section className={`${contact} py-5`} id="contact">
        <div className={`container w-75 ${container} py-4`}>
          <h2 className="main-title">CONTACT ME</h2>
          <Divider />
          <div className={`${form} pt-4`}>
            <div className={`w-100 ${name}`}>
              <label htmlFor="">Name</label>
              <input type="text" className="w-100" placeholder="Name" />
            </div>
            <div className={`w-100 ${email}`}>
              <label htmlFor="">Email Address</label>
              <input
                type="email"
                className="w-100"
                placeholder="Email Address"
              />
            </div>
            <div className={`w-100 ${phone}`}>
              <label htmlFor="">Phone Number</label>
              <input type="tel" className="w-100" placeholder="Phone Number" />
            </div>
            <div className={`w-100 ${message}`}>
              <label htmlFor="">Message</label>
              <textarea className="w-100" placeholder="Message"></textarea>
            </div>
            <button className={`btn btn-lg btn-submit`}>Send</button>
          </div>
        </div>
      </section>
    </>
  );
}
