import React from "react";
import Divider from "../Divider/Divider";
import boxStyle from "./Box.module.css";

const { overlay, closeIcon } = boxStyle;

export default function Box({ close }) {
  return (
    <>
      <div className={`boxContainer d-none ${overlay} p-3 py-5`}>
        <div className="box bg-white d-flex flex-column align-items-center justify-content-center rounded-3 w-75 mx-auto p-5 position-relative ">
          <h2 className="main-title">Habibty Merna</h2>
          <i className={`fa-solid fa-xmark ${closeIcon}`} onClick={close}></i>
          <Divider />
          <div className="content mx-auto d-flex flex-column align-items-center justify-content-center gap-4 w-75">
            <img
              src={require("../../Assets/images/cake.png")}
              className="w-100 rounded-2"
              alt=""
            />
            <p className="m-0 text-center">
              Lorem ipsum dolor sit amet, consectetur adipisicing elit. Mollitia
              neque assumenda ipsam nihil, molestias magnam, recusandae quos
              quis inventore quisquam velit asperiores, vitae? Reprehenderit
              soluta, eos quod consequuntur itaque. Nam.
            </p>
            <button className="btn btn-submit" onClick={close}>
              <i className="fa-solid fa-xmark me-2"></i>Close Window
            </button>
          </div>
        </div>
      </div>
    </>
  );
}
