import React from "react";
import footerStyle from "./Footer.module.css";

const { footer, copyright } = footerStyle;

export default function Footer() {
  return (
    <>
      <footer className={`py-5 ${footer}`}>
        <div className="container w-75">
          <div className="row footer-content text-center">
            <div className="col-lg-4 location">
              <h3 className={`text-uppercase`}>Location</h3>
              <p className="m-0">2215 John Daniel Drive Clark, MO 65243</p>
            </div>
            <div className={`col-lg-4`}>
              <h3 className={`text-uppercase`}>Around the web</h3>
              <div className={`${footerStyle["social-links"]}`}>
                <div className="facebook d-flex justify-content-center align-items-center p-2 rounded-circle">
                  <i className="fa-brands fa-facebook-f"></i>
                </div>
                <div className="twitter d-flex justify-content-center align-items-center p-2 rounded-circle">
                  <i className="fa-brands fa-twitter"></i>
                </div>
                <div className="linkedin d-flex justify-content-center align-items-center p-2 rounded-circle">
                  <i className="fa-brands fa-linkedin-in"></i>
                </div>
                <div className="github d-flex justify-content-center align-items-center p-2 rounded-circle">
                  <i className="fa-brands fa-github"></i>
                </div>
              </div>
            </div>
            <div className="col-lg-4 about-freelance">
              <h3 className={`text-uppercase`}>About freelance</h3>
              <p className="m-0">
                Freelance is a free to use, MIT licensed Bootstrap theme created
                by Route
              </p>
            </div>
          </div>
        </div>
      </footer>
      <div className={`${copyright} text-center`}>
        <p className="m-0">
          Copyright &copy; 2022 | made with
          <i className="fa-solid fa-heart fa-beat mx-2 text-danger"></i>
          by <a href="https://github.com/usamaalidev">Usama Ali</a>
        </p>
      </div>
    </>
  );
}
