import React from "react";
import avatar from "../../Assets/images/avatar.svg";
import Divider from "../Divider/Divider";
import interfaceStyle from "./Interface.module.css";

const { interfaceClass, container } = interfaceStyle;

export default function Interface() {
  return (
    <>
      <header className={interfaceClass}>
        <div className={`${container} w-75`}>
          <img
            src={avatar}
            className={interfaceStyle["main-img"]}
            alt="avatar image"
          />
          <h2 className="m-0 pt-3">Start React</h2>
          <Divider />
          <p className={`m-0`}>Graphic Artist - Web Designer - Illustrator</p>
        </div>
      </header>
    </>
  );
}
